.experience-item {
  display: flex;
  gap: 24px;
  margin-bottom: 32px;
  align-items: flex-start;
}

.experience-logo {
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.experience-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.experience-details {
  flex: 1;
}

.experience-header {
  margin-bottom: 4px;
}

.company-name {
  font-weight: 600;
  font-size: 18px;
}

.company-note,
.current-badge {
  font-style: italic;
  font-weight: 400;
  margin-left: 8px;
  opacity: 0.7;
}

.role {
  font-size: 16px;
  margin-bottom: 2px;
}

.team {
  font-style: italic;
  font-size: 15px;
  margin-bottom: 8px;
  opacity: 0.8;
}

.description {
  font-size: 16px;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .experience-item {
    gap: 16px;
  }
  
  .experience-logo {
    width: 60px;
    height: 60px;
  }
}

