.miscellaneous {
  margin-bottom: 80px;
}

.misc-list {
  list-style: none;
  padding-left: 0;
  margin-top: 16px;
}

.misc-list li {
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 1.7;
  padding-left: 20px;
  position: relative;
}

.misc-list li::before {
  content: '•';
  position: absolute;
  left: 0;
  color: #1a1a1a;
  opacity: 0.5;
}

